.results-container {
  position: relative;
  width: 100%;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
}

.results-background {
  position: absolute;
  top: 0;
  left: 0;
  object-fit: cover;
  z-index: 1;
}

.results-content {
  position: relative;
  z-index: 2;
  background: rgba(255, 255, 255, 0.9);
  border-radius: 15px;
  padding: 30px;
  max-width: 600px;
  width: 90%;
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
}

.results-title {
  font-family: 'Kindergarten', cursive;
  font-size: 48px;
  text-align: center;
  margin-bottom: 30px;
  color: #333;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.result-item {
  display: flex;
  align-items: center;
  padding: 15px;
  width: 1000px;
  justify-content: space-between;
}

.position {
  font-family: 'Kindergarten', cursive;
  font-size: 32px;
  font-weight: bold;
  color: #333;
  min-width: 50px;
  text-align: center;
  margin-right: 20px;
}

.racer-info {
  display: flex;
  align-items: center;
  flex: 1;
  gap: 15px;
}

.racer-image {
  object-fit: contain;
}

.racer-name {
  margin-top: 20px;
  font-family: 'Kindergarten', cursive;
  font-size: 56px;
  font-weight: bold;
  color: #333;
  flex: 1;
  margin-left: 20px;
}

.finish-time {
  margin-top: 20px;
  font-family: 'Kindergarten', cursive;
  font-size: 50px;
  font-weight: bold;
  color: black;
  min-width: 120px;
  text-align: right;
  margin-left: auto;
}

.continue-button {
  position: absolute;
  font-family: KGPerfectPenmanship;
  z-index: 100000;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
}

.continue-button:hover {
  transform: translateY(-2px);
}

.continue-button:active {
  transform: translateY(0);
}