.game-container {
  position: relative;
  background-color: #f0f0f0;
  overflow: hidden;
}

/* Money Display */
.money-display {
  background: linear-gradient(135deg, #f39c12 0%, #e67e22 100%);
  border-radius: 25px;
  padding: 12px 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  box-shadow: 0 4px 15px rgba(243, 156, 18, 0.3);
  z-index: 15;
  border: 2px solid #d68910;
}

.money-icon {
  font-size: 20px;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
}

.money-amount {
  color: white;
  font-size: 18px;
  font-weight: 700;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  min-width: 60px;
  text-align: center;
}

.day-icon {
  font-size: 20px;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
}

.day-amount {
  color: white;
  font-size: 18px;
  font-weight: 700;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  min-width: 80px;
  text-align: center;
}

/* Happiness Display */
.happiness-display {
  background: linear-gradient(135deg, #e91e63 0%, #c2185b 100%);
  border-radius: 25px;
  padding: 12px 20px;
  display: flex;
  align-items: center;
  gap: 8px;
  box-shadow: 0 4px 15px rgba(233, 30, 99, 0.3);
  z-index: 15;
  border: 2px solid #ad1457;
}

.happiness-icon {
  font-size: 20px;
  filter: drop-shadow(0 2px 4px rgba(0, 0, 0, 0.2));
}

.happiness-amount {
  color: white;
  font-size: 18px;
  font-weight: 700;
  text-shadow: 0 2px 4px rgba(0, 0, 0, 0.3);
  min-width: 60px;
  text-align: center;
}

/* Next Day Button */
.next-day-button {
  position: absolute;
  font-family: KGPerfectPenmanship;
  bottom: 20px;
  right: 20px;
  padding: 10px 20px;
  font-size: 30px;
  background-color: #4CAF50;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.3);
}

.next-day-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(39, 174, 96, 0.4);
}

.next-day-button:active {
  transform: translateY(0);
}

/* Top buttons styling */
.top-buttons-container {
  position: absolute;
  top: 20px;
  left: 0;
  right: 0;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 0 40px;
  z-index: 10;
  margin-left: 165px;
  gap: 12px;
}

.image-button {
  background: transparent;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  background-size: 100% 100%;
  background-repeat: no-repeat;
  background-position: center;
  width: 100%;
  height: 98px;
  padding: 0;
}

.image-button:hover {
  transform: translateY(-4px);
  filter: brightness(1.1);
}

.image-button:active {
  transform: translateY(0);
}

.capsule-button {
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  border: none;
  border-radius: 25px;
  color: white;
  padding: 12px 24px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.2);
}

.capsule-button:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(0, 0, 0, 0.3);
}

.capsule-button:active {
  transform: translateY(0);
}

/* Quarter circle styling */
.quarter-circle-container {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 5;
}

.quarter-circle-svg {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 2;
}

.day-text {
  font-size: 16px;
  font-weight: bold;
  fill: #333;
  text-anchor: middle;
}

.race-text {
  font-size: 12px;
  font-weight: 600;
  fill: #555;
  text-anchor: middle;
}

/* Popup Overlay */
.popup-overlay {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1000;
  animation: fadeIn 0.3s ease-in-out;
}

.popup-overlay.fade-out {
  animation: fadeOut 0.3s ease-in-out forwards;
}

.popup-content {
  background: white;
  border-radius: 15px;
  padding: 30px;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  text-align: center;
  max-width: 400px;
  width: 90%;
  animation: slideIn 0.3s ease-out;
}

.popup-content.slide-out {
  animation: slideOut 0.3s ease-in forwards;
}

.popup-content h3 {
  margin: 0 0 15px 0;
  color: #333;
  font-size: 24px;
  font-weight: 600;
}

.popup-content p {
  margin: 0 0 25px 0;
  color: #666;
  font-size: 16px;
  line-height: 1.5;
}

.popup-buttons {
  display: flex;
  gap: 15px;
  justify-content: center;
}

.popup-button {
  padding: 12px 30px;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  font-weight: 600;
  cursor: pointer;
  transition: all 0.3s ease;
  min-width: 100px;
}

.popup-button.cancel {
  background: linear-gradient(135deg, #ff6b6b 0%, #ee5a52 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(255, 107, 107, 0.3);
}

.popup-button.cancel:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(255, 107, 107, 0.4);
}

.popup-button.confirm {
  background: linear-gradient(135deg, #4ecdc4 0%, #44a08d 100%);
  color: white;
  box-shadow: 0 4px 15px rgba(78, 205, 196, 0.3);
}

.popup-button.confirm:hover {
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(78, 205, 196, 0.4);
}

.popup-button:active {
  transform: translateY(0);
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}

@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}

@keyframes slideIn {
  from {
    opacity: 0;
    transform: translateY(-20px) scale(0.9);
  }
  to {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
}

@keyframes slideOut {
  from {
    opacity: 1;
    transform: translateY(0) scale(1);
  }
  to {
    opacity: 0;
    transform: translateY(20px) scale(0.9);
  }
}