.pick-bozo-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 100vh;
  background: linear-gradient(135deg, #667eea 0%, #764ba2 100%);
  padding: 2rem;
  font-family: 'Arial', sans-serif;
}

.pick-bozo-title {
  color: white;
  font-size: 3rem;
  font-weight: bold;
  margin-bottom: 0.5rem;
  text-align: center;
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.pick-bozo-subtitle {
  color: rgba(255, 255, 255, 0.9);
  font-size: 1.2rem;
  margin-bottom: 3rem;
  text-align: center;
}

.characters-grid {
  display: flex;
  gap: 2rem;
  flex-wrap: wrap;
  justify-content: center;
  max-width: 1200px;
}

.character-item {
  cursor: pointer;
  transition: all 0.3s ease;
  border: 3px solid transparent;
  border-radius: 10px;
  overflow: hidden;
}

.character-item:hover {
  transform: translateY(-10px);
  border-color: #667eea;
  box-shadow: 0 20px 40px rgba(0, 0, 0, 0.3);
}

.character-item.selected {
  border-color: #4CAF50;
  transform: translateY(-5px);
  box-shadow: 0 15px 30px rgba(76, 175, 80, 0.4);
}

.character-image {
  width: 100%;
  height: 100%;
  object-fit: cover;
  display: block;
}

.selection-summary {
  margin-top: 2rem;
  background: rgba(255, 255, 255, 0.95);
  border-radius: 20px;
  padding: 2rem 3rem;
  text-align: center;
  box-shadow: 0 10px 30px rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(10px);
  min-width: 300px;
}

.selected-character-name {
  margin: 0 0 0.5rem 0;
  font-size: 2rem;
  font-weight: bold;
  color: #333;
}

.selected-character-description {
  margin: 0;
  font-size: 1.2rem;
  color: #666;
  line-height: 1.4;
}

/* Responsive design */
@media (max-width: 768px) {
  .pick-bozo-title {
    font-size: 2rem;
  }
  
  .pick-bozo-subtitle {
    font-size: 1rem;
  }
  
  .characters-grid {
    gap: 1rem;
    flex-direction: column;
    align-items: center;
  }
  
  .character-item {
    min-width: 180px;
    max-width: 200px;
  }
  
  .selection-summary {
    min-width: 250px;
    padding: 1.5rem 2rem;
  }
  
  .selected-character-name {
    font-size: 1.5rem;
  }
  
  .selected-character-description {
    font-size: 1rem;
  }
} 